$Brcolor: #653F4C/*коричневые цвета*/
$Becolor: #E5D4CD/*бежевые цвета*/
body
    background-color: #E5D4CD
    max-width: 1900px
.header 
    background-color: $Brcolor
    display: flex
    justify-content: space-between
    align-items: center
    position: relative
    width: 100%
    height: 73px
    flex-wrap: nowrap
    color: #E5D4CD
li
    display: inline-block
    padding: 50px
.home-page__line
    color: $Becolor
    width: 600px
    height: 0px
    stroke-width: 1px
    stroke: #000
.link
    text-decoration: none
    color: $Becolor
    font-family: Manrope
    font-size: 18px
    font-style: normal
    font-weight: 400
.home-page__title 
    font-size: 4vw
    font-family: "Crimson Pro"
    font-weight: 700
    color: $Brcolor
    margin-bottom: 0
.home-page
    display: flex
.home-page__text-area
    display: flex
    flex-direction: column
    align-items: center
    width: 30vw
    
    justify-content: start
    align-items: end
.home-page__images
    max-width: 50vw
    padding: 100px 30px
    width: 70vw
    margin: 0 auto
    display: flex
    position: relative
.home-page__img
    position: relative
    padding-top: 30px 
    padding-right: 20%
    width: 35vw
    z-index: 2
.home-page__background
    position: absolute
    width: 38vw
    top: 165px
    right: 150px
    border-radius: 0px 100px 0px 0px
    opacity: 0.25
    z-index: -1
#logo
    object-fit: contain
.button
    display: flex
    width: 8vw
    height: 5vh
    padding: 25px 85px 25px 85px
    justify-content: center
    align-items: center
    gap: 10px
    flex-shrink: 0
    border-radius: 25px
    border: 2px solid $Brcolor
    background-image: url("./images/free-icon-right-arrow-2931527.png") 
    background-repeat : no-repeat
.button__text
    text-align: start
    font-family: Manrope
    font-size: 1.5rem
    font-style: normal
    font-weight: 400
    line-height: 125% /* 25px */
    letter-spacing: 2.4px
    text-decoration-line: underline
    text-transform: uppercase
    color: $Brcolor
    margin-top: 200px
    width: 400px






